import React from 'react';
import '../scss/Setting.scss';
import { Link } from 'react-router-dom';

class Setting extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            playerNum: 3,
            timer: 60,
            spyMode: false,
            theme: "",
            themeKr: "",
            easterEgg: "false",
            apiData: null
        }
    }

    componentDidMount = () => {
        if (this.props.globalState.easterEgg !== "") {this.setState({easterEgg:this.props.globalState.easterEgg})};
        // console.log('Easter Egg:', this.props.globalState.easterEgg);

        // Get data from API
        this.getDataFromDb();
    }
    getDataFromDb = () => {
        fetch('https://liar-game-api.withoutwax.now.sh/api/getData')
            .then((data) => data.json())
            .then((res) => { this.setState({ apiData: res}) })
    }

    setPlayerNum = (event) => {
        this.setState({playerNum: Number(event.target.value)});
    }
    setTimer = (event) => {
        if (event.target.value === "unlimited") {
            this.setState({timer: event.target.value});
        } else {
            this.setState({timer: Number(event.target.value)});
        }
    }
    spyModeSelect = (event) => {
        // console.log(this.state.spyMode);
        console.log(event.target.type);
        let value = event.target.type === "checkbox" ? event.target.checked : event.target.value;

        this.setState({
            spyMode: value
        });
    }
    setTheme = (event) => {
        // console.log(event.target.textContent);
        this.setState({
            theme: event.target.value,
            themeKr: event.target.textContent
        });
    }

    updateGlobalState = () => {
        this.props.parentCallbackState(this.state);
    }

    render() {
        
        // Display ê²Œì„ ì‹œì‘! button when the user chooses the theme.
        let startGameButton = this.state.theme !== "" ? (<Link to='/game' onClick={this.updateGlobalState}>ê²Œì„ì‹œì‘!</Link>) : ``;

        let themeButton = [];
        // After apiData state has value from the API
        if (this.state.apiData) {
            // console.log(this.state.apiData.data);
            themeButton = this.state.apiData.data.map(theme => {
                // console.log(theme);
                // theme.type = "food"
                // theme.typeKr = "ìŒì‹"
                // theme.easterEgg = "false"
                return theme.easterEgg === "false" || theme.easterEgg === this.state.easterEgg ? <button value={theme.type} onClick={this.setTheme} key={theme.type}>{ theme.typeKr }</button> : ""
            });
        }
        // console.log(themeButton);
        

        return (
            <section className="setting-container">
                <h1>ì„¤ì • ì°½</h1>

                <form className="setting-form">
                    <label className="player-num">
                        <h2>ì°¸ì—¬ì¸ì›:</h2>
                        <select value={this.state.value} onChange={this.setPlayerNum}>
                            <option value="3">3</option>
                            <option value="4">4</option>
                            <option value="5">5</option>
                            <option value="6">6</option>
                            <option value="7">7</option>
                            <option value="8">8</option>
                            <option value="9">9</option>
                            <option value="10">10</option>
                            <option value="11">11</option>
                            <option value="12">12</option>
                            <option value="13">13</option>
                            <option value="14">14</option>
                            <option value="15">15</option>
                            <option value="16">16</option>
                            <option value="17">17</option>
                            <option value="18">18</option>
                            <option value="19">19</option>
                            <option value="20">20</option>
                        </select>
                    </label>
                    <label className="set-timer">
                        <h2>ì œí•œì‹œê°„:</h2>
                        <select value={this.state.value} onChange={this.setTimer}>
                            <option value="60">60 ì´ˆ</option>
                            <option value="90">90 ì´ˆ</option>
                            <option value="120">120 ì´ˆ (2ë¶„)</option>
                            <option value="150">150 ì´ˆ (2ë¶„ 30ì´ˆ)</option>
                            <option value="180">180 ì´ˆ (3ë¶„)</option>
                            <option value="240">240 ì´ˆ (4ë¶„)</option>
                            <option value="300">300 ì´ˆ (5ë¶„)</option>
                            <option value="unlimited">ë¬´ì œí•œ</option>
                        </select>
                    </label>
                    <label className="spy-mode">
                        <span className="caption" style={{fontSize:1+'rem'}}>**ìŠ¤íŒŒì´ ëª¨ë“œëŠ” ì¤€ë¹„ ì¤‘ì…ë‹ˆë‹¤!**</span>
                        {/* {`ìŠ¤íŒŒì´ ëª¨ë“œ: ${this.state.spyMode}`}
                        <input 
                            name="spyMode"
                            type="checkbox"
                            checked={this.state.spyMode}
                            onChange={this.spyModeSelect}
                        /> */}
                    </label>
                </form>

                <div className="theme-select">
                    <h2>ì£¼ì œ: {`${this.state.themeKr}`}</h2>                    
                    {themeButton.map(button => {
                        return button
                    })}
                </div>
                
                <div>
                    {startGameButton}
                </div>
                
            </section>
            );
    }
}

export default Setting;