import React from 'react';

class Select extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            playerNum: this.props.globalState.playerNum || 3,
            spyMode: this.props.globalState.spyMode,
            theme: this.props.globalState.theme || "food",
            vocab: "",
            liar: 1,
            buttonDisabled: [],
            displayStatus: "í”Œë ˆì´ì–´ë¥¼ ì„ íƒí•´ì£¼ì„¸ìš”",
            buttonDisabledText: "í™•ì¸í–ˆìŠµë‹ˆë‹¤!",
            beginGame: false,
            showCardStatus: false,
            easterEgg: "",
            apiData: this.props.globalState.apiData,
            selectData: null
        }
    }

    componentDidMount = () => {
        // Set Easter Egg
        if (this.props.globalState.easterEgg !== "") {this.setState({easterEgg:this.props.globalState.easterEgg})};
        
        // If the API is not present
        let chosenTheme;
        let data;
        if (this.state.apiData === null) {
            // console.log("apidata == null");
            chosenTheme = {
                "food": require('../data/food.json'),
                "place": require('../data/place.json'),
                "occupation": require('../data/occupation.json'),
                "biblecharacter": require('../data/biblecharacter.json'),
                "onnurichanyangteammember": require('../data/onnurichanyangteammember.json')
            }
            // console.log(chosenTheme[this.state.theme].kr);
            data = chosenTheme[this.state.theme].kr;
        } else {
            // console.log("apidata == api");
            for (let i = 0; i < this.state.apiData.data.length; i++) {
                let words = this.state.apiData.data[i];
                if (words.type === this.state.theme) {
                    // console.log(words);
                    chosenTheme = words.kr;
                }
                // chosenTheme = this.state.apiData.data.kr
            }
            data = chosenTheme;
        }
        this.setState({ selectData: data });
        this.generateRandomNumber(data);
    }
    
    generateRandomNumber = (data) => {
        let randomIndex = Math.floor(Math.random() * data.length);
        let chooseLiar = Math.floor(Math.random() * this.state.playerNum);

        this.setState({ 
            vocab: data[randomIndex],
            liar: chooseLiar
        });
    }

    showCard = (event) => {
        let button = Number(event.target.id);
        
        if (this.state.buttonDisabled.includes(button) === false) {

            this.setState({
                buttonDisabled: this.state.buttonDisabled.concat(button)
            });
        }

        let card = event.target.className;
        
        if (card.includes("no-liar")) {
            this.setState({
                displayStatus: `ë‹¹ì‹ ì€ ë¼ì´ì–´ê°€ ì•„ë‹™ë‹ˆë‹¤. ì´ë²ˆì— ì„ íƒëœ ë‹¨ì–´ëŠ”:`,
                playerState: false
            });
        } else {
            this.setState({
                displayStatus: `ë‹¹ì‹ ì€`,
                playerState: true
            });
        }

        // Hide player select card during check
        this.setState({
            showCardStatus: true
        })
    }

    resetDisplayStatus = () => {
        if (this.state.buttonDisabled.length === this.state.playerNum) {
            // All Player has been selected
            console.log("ëª¨ë“  í”Œë ˆì´ì–´ê°€ ì„ íƒ ë˜ì—ˆìŠµë‹ˆë‹¤");

            // Begin Timer
            this.setState({ 
                displayStatus: "ê²Œì„ì´ ì‹œì‘ë˜ì—ˆìŠµë‹ˆë‹¤!",
                beginGame: true 
            });
            this.props.nextStage(2);
            this.props.setVocab(this.state.vocab, this.state.selectData);
            
        } else {
            if (this.state.playerNum - this.state.buttonDisabled.length === 1) {
                console.log("One player left");
                this.setState({buttonDisabledText: "ê²Œì„ ì‹œì‘!"});
            }
            this.setState({displayStatus: "í”Œë ˆì´ì–´ë¥¼ ì„ íƒí•´ì£¼ì„¸ìš”"})
        }

        // Show player select card after check
        this.setState({
            showCardStatus: false
        });
    }

    render() {
        console.log("render()");
        // console.log("PROPS:", this.props);
        let defaultText = "ì„ íƒí•˜ì„¸ìš”";
        // console.log(this.state.buttonDisabled.includes(0));
        // console.log("THIS.STATE", this.state);
        
        let playersCard = []
        for (let i = 0; i < this.state.playerNum; i++) {
            if (i === this.state.liar) {
                playersCard.push(<button className={`playersCard liar ${this.state.buttonDisabled.includes(i) ? 'disabled' : ''}`} disabled={this.state.buttonDisabled.includes(i) ? true : false} key={i} id={i} onClick={this.showCard}>{defaultText}</button>)
            } else {
                playersCard.push(<button className={`playersCard no-liar ${this.state.buttonDisabled.includes(i) ? 'disabled' : ''}`} disabled={this.state.buttonDisabled.includes(i) ? true : false} key={i} id={i} onClick={this.showCard}>{defaultText}</button>)
            }
        }
        console.log(this.state);
        let textView;
        if (this.state.buttonDisabled.length > 0 && this.state.showCardStatus === true) {
            textView = this.state.playerState ? <span className="red">ë¼ì´ì–´ ì…ë‹ˆë‹¤.</span> : <span className="green"><br/>{this.state.vocab}</span>;
        } else {
            textView = null;
        }
        let nextButton = this.state.displayStatus === "í”Œë ˆì´ì–´ë¥¼ ì„ íƒí•´ì£¼ì„¸ìš”" ? `` : <button onClick={this.resetDisplayStatus}>{this.state.buttonDisabledText}</button>;

        return (
            <div>
                <div>
                    <h2>{this.state.displayStatus} {textView}</h2>
                    { nextButton }
                </div>
                {this.state.showCardStatus ? '' : playersCard }
            </div>
        );
    }
}

export default Select;